// order-menu
.order-menu {
    padding: $middle 0;
    border-top: 3px solid $color-primary;
    background-color: $color-bg;
    @include pad {
        padding: $middle-md 0;
    }
    @include mobile {
        padding: $small 0;
    }
    .main-pic {
        width: 100%;
        padding-top: 70%;
        background-image: url("../images/Menu/-.JPG");
        background-position: center center;
        background-size: 100%;
        background-repeat: no-repeat;
        cursor: pointer;
        @include pad {
            padding-top: 75%;
        }
    }
    .pic-modal {
        position: fixed;
        background-color: rgba(19, 10, 4, 0.6);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 101;
        display: none;
        .menu-modal {
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            margin: auto;
            width: 85%;
            height: 95%;
            @include pad {
                width: 95%;
                height: 60%;
            }
            @include mobile {
                width: 98%;
                height: 38%;
            }
        }
        .close {
            font-size: 48px;
            color: $color-primary;
            background-color: $color-white;
            border-radius: 50%;
            position: absolute;
            top: 0;
            right: 5.5%;
            cursor: pointer;
            @include pad {
                font-size: 36px;
                top: 17.5%;
                right: 2.5%;
            }
            @include mobile {
                font-size: 24px;
                top: 29%;
                right: 2%;
            }
        }
    }
    .title {
        width: 100%;
        padding-top: 10%;
        background-position: center center;
        background-size: 35%;
        background-repeat: no-repeat;
        border-top: 3px solid $color-secondary;
        position: relative;
        @include pad {
            background-size: 45%;
            padding-top: 15%;
        }
        @include mobile {
            background-size: 65%;
            padding-top: 20%;
        }
        .menu-arrow {
            display: none;
            @include mobile {
                display: block;
                font-size: 24px;
                position: absolute;
                top: 0;
                bottom: 0;
                right: 10%;
                display: flex;
                align-items: center;
                transform: rotate(0deg);
                cursor: pointer;
                animation: moving 0.4s 1s infinite ease-in-out alternate-reverse;
                &.active {
                    transform: rotate(180deg);
                }
            }
        }
        @keyframes moving {
            0% {
                top: 0;
            }
            50% {
                top: 4px;
            }
            100% {
                top: 8px;
            }
        }
    }
    .nav-wrap {
        @include mobile {
            display: none;
        }
    }
    .nav-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: $middle;
        @include mobile {
            margin-bottom: $small;
        }
    }
    .nav {
        width: 24.6%;
        padding-top: 25%;
        background-position: center center;
        background-size: 100%;
        background-repeat: no-repeat;
        position: relative;
        @include pad {
            padding-top: 34%;
            width: 32.7%;
        }
        @include mobile {
            padding-top: 50.5%;
            width: 49.5%;
        }
    }
    .star-group {
        font-size: 20px;
        align-self: flex-end;
        margin-right: $small-md;
        color: #fdc500;
        @include pad {
            font-size: 18px;
        }
        @include mobile {
            font-size: 12px;
            margin-top: 1.5%;
        }
    }
    .price-group {
        width: 100%;
        height: 91%;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        @include pad {
            height: 90%;
        }
        @include mobile {
            height: 93%;
        }
    }
    .price {
        display: flex;
        align-items: center;
        font: normal normal bold 21px/28px "Lato", "Noto Sans TC", sans-serif;
        color: $color-info-2;
        @include pad {
            font: normal normal bold 18px/24px "Lato", "Noto Sans TC", sans-serif;
        }
        @include mobile {
            font: normal normal bold 14px/21px "Lato", "Noto Sans TC", sans-serif;
        }
    }
    .price-mb {
        @include mobile {
            font: normal normal bold 12px/16px "Lato", "Noto Sans TC", sans-serif;
        }
    }
    .mark {
        color: $color-primary;
        margin-left: $small-md;
        font: normal normal bold 18px/26px "Lato", "Noto Sans TC", sans-serif;
        @include pad {
            margin-left: $small-sm;
            font: normal normal bold 14px/21px "Lato", "Noto Sans TC", sans-serif;
        }
        @include mobile {
            font: normal normal bold 12px/16px "Lato", "Noto Sans TC", sans-serif;
        }
    }
    .mark-s {
        margin-left: 0;
    }
    .text {
        width: 100%;
        text-align: center;
        margin-top: $middle-md;
        font: normal normal bold 28px/40px "Lato", "Noto Sans TC", sans-serif;
        @include pad {
            font: normal normal bold 21px/28px "Lato", "Noto Sans TC", sans-serif;
        }
        @include mobile {
            font: normal normal bold 16px/24px "Lato", "Noto Sans TC", sans-serif;
            margin-top: $small-md;
        }
    }
    .special {
        color: $color-primary;
    }

